import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  titolo: PropTypes.string,
  occhiello: PropTypes.string,
  data: PropTypes.string,
});

export const formValues = PropTypes.shape({
  titolo: PropTypes.string,
  occhiello: PropTypes.string,
  data: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
});

export const formTouched = PropTypes.shape({
  titolo: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  occhiello: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  data: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  titolo: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  occhiello: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  data: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
