import { getDefaultOptions, request } from 'api/helpers';

const resource = 'notizies';

export const apiNotizieGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiNotiziePost = async (serviceUrl, notizie) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: notizie ? JSON.stringify(notizie) : null,
  };
  return request(url, options);
};

export const apiNotiziePut = async (serviceUrl, notizie) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: notizie ? JSON.stringify(notizie) : null,
  };
  return request(url, options);
};

export const apiNotizieDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
